define(['knockout', 'jquery'], function (ko, jQuery) {
    ko.bindingHandlers.checkBox = {
        init: function(element, valueAccessor){
            jQuery(element).on("change", function(){
                valueAccessor()(jQuery(this).is(":checked"));
            });
        }
    };
});


